.nav-link {
    color: #343434;
    text-decoration: none;
    min-width: 80px;
}

.nav-link.active {
    color: #000;
    font-weight: 700;
}

#scrollContent {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100%;
    white-space: nowrap;
}

#full{
    display: flex;
    animation: textScrollEffect 12s infinite linear;
}

@keyframes textScrollEffect {
    from {
        transform: translateX(0%)
    }

    to {
        transform: translateX(-100%)
    }
}
