  .navbar-collapse ul li a {
    position: relative;
    overflow: hidden;
    z-index: 1;
  }
  .navbar-collapse ul li a:after {
    display: block;
    position: absolute;
    margin: 0;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: '.';
    color: transparent;
    width: 1px;
    height: 1px;
    border-radius: 50%;
    background: transparent;
  }
   ul li a:hover:after {
    animation: circle 1.5s ease-in forwards;
  }
  @keyframes circle {
    0% {
      width: 1px;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      height: 1px;
      z-index: -1;
      background: rgb(233, 194, 233);
      border-radius: 100%;
    }
    100% {
      background: #cfe95a;
      height: 5000%;
      width: 5000%;
      z-index: -1;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      border-radius: 0;
    }
  }